<template>
    <div >
        <div style="width:100%;height:50px;margin-bottom:20px;">
            <div style="width:25%;display:inline-block;float:left">
                <el-input v-model="parkingName" placeholder="请输入车场名称" ></el-input>
            </div>
            <!-- 过车时间-区间查询 -->
    
            <div style="float:left;margin-left:20px;">
              <el-button type="primary" @click="getCarMsg()">查询</el-button>
            </div>
        </div>
        <!-- 第二个表格 -->
     <el-table
          :data="tableCar"
          style="width: 100%;padding:10px 10px;border-radius: 6px">
          
          <el-table-column
            prop="id"
            label="id"
            width="80">
          </el-table-column>
          <el-table-column
            prop="parkingName"
            label="停车场名称">
          </el-table-column>
          <!-- <el-table-column
            prop="longitude"
            label="经度">
          </el-table-column>
          <el-table-column
            prop="latitude"
            label="纬度">
          </el-table-column> -->
<!-- 
          <el-table-column
            prop="totalSpace"
            label="总车位">
          </el-table-column> -->
          <el-table-column
            prop="cityName"
            label="所属城市">
          </el-table-column>

          <el-table-column
            prop="parkingType"
            label="车场类型">
          </el-table-column>
          <el-table-column
            prop="parkingKind"
            label="车场种类">
          </el-table-column>
          <el-table-column
            prop="parkingOwner"
            label="车场所有人">
          </el-table-column>
          <el-table-column
            prop="onlineReservation"
            label="是否支持网上预约">
          </el-table-column>
          <!-- 操作 -->
          <el-table-column
          label="操作" width='160'>
          <template slot-scope="scope">
              <el-button
                  @click="handleDetailMsg(scope.$index, scope.row,scope)"
                  type="primary"
                  size="small">详情
              </el-button>
              <el-button
                  @click="handleChangeMsg(scope.$index, scope.row)"
                  type="primary"
                  size="small">修改
              </el-button>
          </template>
          </el-table-column>
        </el-table> 
        <!-- 分页条 -->
        <!-- page-sizes, 每页显示条数   
            :page-size 当前页显示条数   feeSystemTradeNo
         -->
        <el-pagination
            background
            layout="total, sizes, prev, pager, next, jumper"
            :total="total" v-show='paginationShow' 
            style='margin-top:10px;'
            :page-sizes="[5,10,20,30]"      
            :page-size="pageSize"
            @size-change="handleSizeChange"
            @current-change="currentChange"
            :current-page="currentPage">
        </el-pagination>

        <!-- 查看模态框 -->
        <el-dialog title='详情信息' :visible.sync='dialogVisibleParking'>
          <div style='font-size:14px;text-align:left;padding:10px 0px 0 25px;'>
            <el-form v-model='lookParkingForm'>
                <el-row >
                  <el-col :span='12'>
                      <span>停车场名称&nbsp;&nbsp;</span> 
                          <el-input  v-model='lookParkingForm.parkingName' style="width:60%;" readonly></el-input>
                  </el-col>
                  <el-col :span='12'>
                      <span>所属城市&nbsp;&nbsp;</span> 
                      <el-input  style="width:60%;" v-model='lookParkingForm.cityName' readonly></el-input>
                  </el-col>
                </el-row>
                <!-- 第二行 -->
                <el-row >
                    <el-col :span='12'>
                        <span>经度坐标&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span> 
                        <el-input  v-model='lookParkingForm.longitude' style="width:60%;" readonly></el-input>
                    </el-col>
                    <el-col :span='12'>
                        <span>纬度坐标&nbsp;&nbsp;</span> 
                        <el-input   style="width:60%;" v-model='lookParkingForm.latitude' readonly></el-input>
                    </el-col>
                </el-row>
                <!--第三行  -->
                <el-row >
                    <el-col :span='12'>
                        <span>车场所有人&nbsp;&nbsp;</span> 
                        <el-input  v-model='lookParkingForm.parkingOwner' style="width:60%;" readonly></el-input>
                    </el-col>
                    <el-col :span='12'>
                        <span>总车位数&nbsp;&nbsp;</span> 
                        <el-input   style="width:60%;" v-model='lookParkingForm.totalSpace' readonly></el-input>
                    </el-col>
                </el-row>
                <!-- 第四行 -->
                <el-row >
                    <el-col :span='12'>
                        <span>车场类型&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span> 
                        <el-select v-model='lookParkingForm.parkingType' readonly style='width:60%'>
                            <el-option
                              v-for='item in parkingTypeOptions'
                              :key="item.id"
                              :label='item.label'
                              :value='item.value'>
                            </el-option>
                          </el-select>
                    </el-col>
                    <el-col :span='12'>
                        <span>车场种类&nbsp;&nbsp;</span> 
                        <el-select v-model='lookParkingForm.parkingKind' readonly style='width:60%'>
                            <el-option
                              v-for='item in parkingKindOptions'
                              :key="item.id"
                              :label='item.label'
                              :value='item.value'>
                            </el-option>
                          </el-select>
                    </el-col>
                </el-row>
                <!-- 第五行 -->
                <el-row >
                    <el-col :span='12'>
                        <span>出入口数量&nbsp;&nbsp;</span> 
                        <el-input  v-model='lookParkingForm.enterExitCount' style="width:40%;"  readonly></el-input>
                    </el-col>
                    <el-col :span='12'>
                        <span>是否网上预约&nbsp;&nbsp;</span> 
                        <el-select v-model='lookParkingForm.onlineReservation' style="width:30%;" readonly>
                          <el-option
                          v-for='item in onlineReservationOptinos'
                          :key='item.key'
                          :value='item.value'
                          :label='item.label'>
                          </el-option>
                        </el-select>
                    </el-col>
                </el-row>
                <!-- 第六行 -->
                <div style='border-bottom:dashed 1px #c8c7cc;margin:20px;'></div>
              <div class='dynamicBox' v-for='(item,index) in lookParkingForm.ztnr' >
                <el-row>
                  <el-col :span='7'>
                    <el-form-item label='进出口ID' class='laeblStyle3'>
                      <el-input v-model='item.enterExitNo' class='handleInput' readonly style='width:60%'></el-input>
                    </el-form-item>
                  </el-col>
                  <!-- <el-col :span='1'>
                    &nbsp;
                  </el-col> -->
                  <el-col :span='7'>
                      <el-form-item label='进出口类型' >
                        <el-input v-model='item.jcklx' style='width:50%' readonly></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span='1'>
                    &nbsp;
                  </el-col>
                  <el-col :span='7'>
                      <el-form-item label='名称'>
                        <el-input v-model='item.enterExitName' style='width:70%' readonly></el-input>
                      </el-form-item>
                    </el-col>
                </el-row>
                <!-- 第二行 -->
                <el-row>
                  <el-col :span='10'>
                      <el-form-item label='经度' >
                          <el-input v-model='item.longitude' style='width:75%' readonly></el-input>
                        </el-form-item>
                  </el-col>
                  <el-col :span='2'>
                    &nbsp;
                  </el-col>
                  <el-col :span='10'>
                      <el-form-item label='纬度' >
                          <el-input v-model='item.latitude' style='width:75%' readonly></el-input>
                        </el-form-item>
                  </el-col>
                </el-row>
              </div>
            </el-form> 
          </div>
        </el-dialog>
        <!-- 修改详情 -->
        <el-dialog title='编辑信息' :visible.sync='dialogEditParking'>
          <div style='font-size:14px;text-align:left;padding:10px 0px 0 25px;'>
            <el-form v-model='changeParkingForm'>
                <el-row  >
                  <el-col :span='12'>
                      <span>停车场名称&nbsp;&nbsp;</span> 
                          <el-input  v-model='changeParkingForm.parkingName' style="width:60%;"></el-input>
                  </el-col>
                  <el-col :span='12'>
                      <span>所属城市&nbsp;&nbsp;</span> 
                      <el-input  style="width:60%;" v-model='changeParkingForm.cityName'></el-input>
                  </el-col>
                </el-row>
                <!-- 第二行 -->
                <el-row >
                    <el-col :span='12'>
                        <span>经度坐标&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span> 
                        <el-input  v-model='changeParkingForm.longitude' style="width:60%;"></el-input>
                    </el-col>
                    <el-col :span='12'>
                        <span>纬度坐标&nbsp;&nbsp;</span> 
                        <el-input   style="width:60%;" v-model='changeParkingForm.latitude'></el-input>
                    </el-col>
                </el-row>
                <!--第三行  -->
                <el-row >
                    <el-col :span='12'>
                        <span>车场所有人&nbsp;&nbsp;</span> 
                        <el-input  v-model='changeParkingForm.parkingOwner' style="width:60%;"></el-input>
                    </el-col>
                    <el-col :span='12'>
                        <span>总车位数&nbsp;&nbsp;</span> 
                        <el-input   style="width:60%;" v-model='changeParkingForm.totalSpace'></el-input>
                    </el-col>
                </el-row>
                <!-- 第四行 -->
                <el-row >
                    <el-col :span='12'>
                        <span>车场类型&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span> 
                        <el-select v-model='changeParkingForm.parkingType' clearable>
                            <el-option
                              v-for='item in parkingTypeOptions'
                              :key="item.id"
                              :label='item.label'
                              :value='item.value'>
                            </el-option>
                          </el-select>
                    </el-col>
                    <el-col :span='12'>
                        <span>车场种类&nbsp;&nbsp;</span> 
                        <el-select v-model='changeParkingForm.parkingKind' clearable>
                            <el-option
                              v-for='item in parkingKindOptions'
                              :key="item.id"
                              :label='item.label'
                              :value='item.value'>
                            </el-option>
                          </el-select>
                    </el-col>
                </el-row>
                <!-- 第五行  编辑模态框-->
                <el-row >
                    <el-col :span='12'>
                        <span>出入口数量&nbsp;&nbsp;</span> 
                        <el-input  v-model='changeParkingForm.enterCount' style="width:40%;" @blur="handleBlurEnter(changeParkingForm.enterCount)"></el-input>
                        <el-button type='button' class='el-button el-button--primary' @click='getEnterMsg(changeParkingForm.enterCount)'>修改</el-button>
                    </el-col>
                </el-row>

                <!-- 第六行 -->
                <el-row >
                    <el-col :span='12'>
                        <span>是否网上预约&nbsp;&nbsp;</span> 
                        <el-select v-model='changeParkingForm.onlineReservation' style="width:30%;">
                          <el-option
                          v-for='item in onlineReservationOptinos'
                          :key='item.key'
                          :value='item.value'
                          :label='item.label'>
                          </el-option>
                        </el-select>
                    </el-col>
                </el-row>
                <!-- 第七行 -->
                <!-- <el-row> -->
                    <div style='float:right;margin:20px 15% 0 0'>
                        <el-button class="el-button el-button--warning" style='margin-right:20px;width:80px' @click='handleCancel()'>取消</el-button>
                      <el-button class="el-button el-button--primary" style='width:80px' @click='handleSave()'>保存</el-button>
                    </div>
                    <div style="clear:both"></div>
                <!-- </el-row> -->
            </el-form> 
          </div>
        </el-dialog>

        <!-- 修改——出入口方向模态框 -->
        <el-dialog title='出入口方向' :visible.sync='dialogVisibleEnter'  width='1000px'>
            <el-form v-model='enterExitForm'>
              <!-- 主体内容 -->
                <div  v-for='(item,index) in enterExitForm.ztnr' :key='index' style='margin-bottom:10px;position:relative'>
                  <div class='dynamicBox'>
                    <el-row >
                        <el-col :span='6' >
                            <el-form-item label='进出口ID' class='laeblStyle2'>
                              <el-input v-model='item.enterExitNo' class='handle-input' style='width:60%'></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span='1'>
                          &nbsp;
                        </el-col>
                      <el-col :span='7'>
                        <el-form-item label='进出口类型' class='laeblStyle'>
                            <el-select v-model='item.jcklx' class='handle-input' >
                                <el-option
                                v-for='item in typeNoOptions'
                                :key='item.id'
                                :label='item.label'
                                :value='item.value'>
                                </el-option>
                            </el-select>
                        </el-form-item>
                      </el-col>
                      <el-col :span='8' >
                          <el-form-item label='名称' >
                            <el-input v-model='item.enterExitName'  style="width:80%"></el-input>
                          </el-form-item>
                      </el-col>
                  </el-row>
                    <!-- 名称、编号、 -->
                    <el-row >
                    <el-col :span='6' >
                        <el-form-item label='经度' class='laeblStyle2'>
                          <el-input v-model='item.enterExitLongitude' class='handle-input'></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span='1'>
                        &nbsp;
                      </el-col>
                    <el-col :span='6' >
                        <el-form-item label='纬度' class='laeblStyle'>
                          <el-input v-model='item.enterExitLatitude' class='handle-input'></el-input>
                        </el-form-item>
                    </el-col>
                  </el-row>
                </div>
              </div>
                  <div style='float:right;margin:20px 15% 0  0'>
                      <el-button type='button' class='el-button el-button--warning' style="width: 80px" @click='cancelEnter()'>取消</el-button>
                      <el-button type='button' class='el-button el-button--primary' style="width: 80px" @click='saveEnter()'>保存</el-button>
                  </div>
                  <div style="clear:both"></div>
            </el-form>
        </el-dialog>

    </div>
    </template>
    <script>
      import moment from 'moment'
      import qs from 'qs'
      import * as carApi from '../../api/device'
      export default {
        data() {
          return {
            enterExitFormArr:[],
            lookParkingForm:{
              ztnr:[
                {"jcklx":"","enterExitName":"","enterExitNo":"","enterExitLongitude":"","enterExitLatitude":""}
              ],
            },
            parkingName:"",
            tableCar:[],
            paginationShow:Boolean,
            total:0,
            currentPage:1,
            pageSize:10,
            dialogVisibleParking:false,
            row:Object,
            dialogEditParking:false,
            row2:Object,
            changeParkingForm:{//初始化
            }, 
            VueDateHX:[],
            //编辑页面
            parkingTypeOptions:[
              {id:"1",value:"1",label:"运营"},
              {id:"2",value:"2",label:"非运营"},
            ],
            parkingKindOptions:[
				      {id:"1",value:"1",label:"封闭式"},
				      {id:"2",value:"2",label:"开放式"},
            ],
            //入口方向数据
            dialogVisibleEnter:false,
            enterForm:{}, //入口表单
            enterCount:[],  //入口数量
            /*出口方向数据*/
            exitCount:[],
            dialogVisibleExit:false,
            exitForm:{},
            saveEditMsg:[],
            onlineReservationOptinos:[
              {id:1,value:1,label:"是"},
              {id:2,value:2,label:"否"},
            ],
            enterExitForm:{
              ztnr:[
                {"jcklx":"","enterExitName":"","enterExitNo":"","enterExitLongitude":"","enterExitLatitude":""}
              ],
            },
            enterExitLength:[],
            typeNoOptions:[],
            enterExitCount:0,
            enterArr:[]
          }
        },
        methods: {
          lookCancel(){
            this.dialogVisibleParking=false;
          },
          lookSave(){
            this.dialogVisibleParking=false;
          },
          handleSave(){
            var self=this;
            console.log('看下表单数据');
            for(var i=0;i<self.enterExitForm.ztnr.length;i++){
              self.changeParkingForm.enterExitInfos[i]={
                "enterExitType":self.enterExitForm.ztnr[i].enterExitType,  
                "typeCount":1,
                "enterExitNo":self.enterExitForm.ztnr[i].enterExitNo,  
                "enterExitName": self.enterExitForm.ztnr[i].enterExitName,  
                "enterExitLongitude": self.enterExitForm.ztnr[i].enterExitLongitude,   
                "enterExitLatitude": self.enterExitForm.ztnr[i].enterExitLatitude,  
                "enterExitKind": self.enterExitForm.ztnr[i].jcklx
              }
            }
            var params=this.changeParkingForm
            var url=this.baseUrl +"parkingInfo/update";
            this.$axios.post(url,params).then(function(res){
              if(res.data.status=='0'){
                self.$message({
                  message:res.data.message,
                  type:"success"
                })
                //调用查询
                self.getCarMsg();
              }else{
                self.$message({
                  message:res.data.message,
                  type:"info"
                })
              }
            }).catch(function(err){
              console.log(err);
            })
            this.dialogEditParking=false;
          },
          handleCancel(){
            this.$message({
              message:"已取消!",
              type:"info"
            })
            this.dialogEditParking=false;
          },
          getEnterMsg(val){
            //修改的时候
            var self=this;
            console.log("修改按钮...." +val)
            this.dialogVisibleEnter=true;
          },
          saveExit(){
            this.$message({
              message:"保存成功!",
              type:"success"
            })
            this.dialogVisibleExit=false;
          },
          cancelExit(){
            this.$message({
              message:"已取消!",
              type:"info"
            })
            this.dialogVisibleExit=false;
          },
          saveEnter(){
            var self =this;
            console.log(self.enterExitForm.ztnr);
            self.$message({
              message:"保存成功!",
              type:"success"
            })
            this.dialogVisibleEnter=false;
          },
          cancelEnter(){
            this.dialogVisibleEnter=false;
          },
          //出口方法
          currentChange(currentPage){
            console.log("currentPage")
            console.log(currentPage)
              var self=this;
              this.currentPage=currentPage;
              var url=this.baseUrl+"parkingInfo/list";

                var paramString={
                  "pageSize":this.pageSize,
                  "currentPage":this.currentPage
                }
                var jsonParams=JSON.stringify(paramString);
              this.axiosMsg(url,jsonParams);
          },
          handleSizeChange(val){
              this.pageSize=val;
              var self=this;
              var url=this.baseUrl+"parkingInfo/list";

              var str={
                "pageSize":this.pageSize,
                "currentPage":this.currentPage
              }
              var jsonParams=JSON.stringify(str);
               this.axiosMsg(url,jsonParams);
              // console.log(`每页显示${val}条`)
          },
          axiosMsg(url,params){
            var self=this;
            this.paginationShow=true;
            this.$axios({
              method:"post",
              url:url,
              headers:{
                "Content-Type":"application/json"
              },
              data:params
            }).then(function(response){
              //判断请求状态
              if(response.data.status=='0'){
                self.$message({
                  message:response.data.message,
                  type:"success"
                })
                //车场类型
                for(var i=0;i<response.data.data.records.length;i++){
                  if(response.data.data.records[i].parkingKind=='1'){
                    response.data.data.records[i].parkingKind='封闭式'
                  }else if(response.data.data.records[i].parkingKind=='2'){
                    response.data.data.records[i].parkingKind='开放式'
                  }
                  if(response.data.data.records[i].parkingType=='1'){
                    response.data.data.records[i].parkingType='运营'
                  }else if(response.data.data.records[i].parkingType=='2'){
                    response.data.data.records[i].parkingType='非运营'
                  }
                  if(response.data.data.records[i].onlineReservation=='0'){
                    response.data.data.records[i].onlineReservation='否'
                  }else if(response.data.data.records[i].onlineReservation=='1'){
                    response.data.data.records[i].onlineReservation='是'
                  }
                }
                self.tableCar=response.data.data.records;
                self.total=response.data.data.total;
              }else{
                self.$message({
                  message:response.data.message,
                  type:"success"
                })
              }

            }).catch(function(err){
              console.log(err);
            })
          },
          momentTime(val){
              return moment(val).format("YYYY-MM-DD HH:mm:ss");
          },
          getCarMsg(){
            var self=this;
              var url=self.baseUrl + "parkingInfo/list";
              //查询，获取参数
              var str={
                'parkingName':self.parkingName,
              }
              var jsonParams=JSON.stringify(str);
              this.axiosMsg(url,jsonParams);
          },
          handleDetailMsg(index,row,scope){
            var self=this;
            self.row=row;
            this.dialogVisibleParking=true;
            var url=self.baseUrl+"parkingInfo/detail/"+row.id;
            self.$axios.get(url).then(function(res){
              console.log('回显接口.....')
              console.log(res.data.data)
              self.lookParkingForm=res.data.data;
              self.lookParkingForm.parkingType=String(self.lookParkingForm.parkingType);
              self.lookParkingForm.parkingKind=String(self.lookParkingForm.parkingKind);
              self.lookParkingForm.ztnr=[];
              console.log(self.lookParkingForm.ztnr)
              for(var i=0;i<res.data.data.enterExitInfos.length;i++){
                self.lookParkingForm.ztnr.push({
                  "enterExitNo":res.data.data.enterExitInfos[i].enterExitNo,
                  "jcklx":res.data.data.enterExitInfos[i].typeName,
                  "enterExitName":res.data.data.enterExitInfos[i].enterExitName,
                  "longitude":res.data.data.enterExitInfos[i].enterExitLongitude,
                  "latitude":res.data.data.enterExitInfos[i].enterExitLatitude,
                  "enterExitType":res.data.data.enterExitInfos[i].typeName,
                })
              }
            }).catch(function(err){
              console.log(err);
            })
          },
          handleChangeMsg(index,row){
            var resArr=[];
            var self=this;
            var id=row.id;
            self.changeParkingForm={};
            self.enterExitForm.ztnr=[];
            var url=self.baseUrl +"parkingInfo/detail/"+id;
            self.dialogEditParking=true;
            self.$axios.get(url).then(function(res){
              console.log('修改回显请求。。。。。')
              console.log(res.data.data.enterExitInfos)
              self.VueDateHX=res.data.data.enterExitInfos;
              self.changeParkingForm=res.data.data;
              self.changeParkingForm.parkingType=String(res.data.data.parkingType);
              self.changeParkingForm.parkingKind=String(res.data.data.parkingKind);
              self.changeParkingForm.enterCount=res.data.data.enterExitCount;
              for(var i=0;i<res.data.data.enterExitInfos.length;i++){
                self.enterExitForm.ztnr.push({
                  "enterExitNo":res.data.data.enterExitInfos[i].enterExitNo,
                  "jcklx":(res.data.data.enterExitInfos[i].enterExitKind),
                  "enterExitName":res.data.data.enterExitInfos[i].enterExitName,
                  "enterExitLongitude":res.data.data.enterExitInfos[i].enterExitLongitude,
                  "enterExitLatitude":res.data.data.enterExitInfos[i].enterExitLatitude,
                  "enterExitType":(res.data.data.enterExitInfos[i].enterExitKind),
                })
              }
              self.enterExitFormArr=self.enterExitForm.ztnr;
              console.log(self.enterExitFormArr)
            }).catch(function(err){
            })
            self.row2=row;
          },
          //入口失去焦点
          handleBlurEnter(val){
            var self=this;
            console.log("失去焦点"+val)
            self.enterExitForm.ztnr=[];
            for(var i=0;i<val;i++){
              var dataArr=self.enterExitFormArr;
              self.enterExitForm.ztnr.push({
                "enterExitNo":!dataArr[i].enterExitNo?"":dataArr[i].enterExitNo,
                "jcklx":!dataArr[i].jcklx?"":dataArr[i].jcklx,
                "enterExitName":!dataArr[i].enterExitName?"":dataArr[i].enterExitName,
                "enterExitLongitude":!dataArr[i].enterExitLongitude?"":dataArr[i].enterExitLongitude,
                "enterExitLatitude":!dataArr[i].enterExitLatitude?"":dataArr[i].enterExitLatitude,
              })
            }
            this.dialogVisibleEnter=true;
          },
          getTypeOptions(){
          var self=this;
          var url=self.baseUrl+'parkingInfo/enterExitType'
            self.$axios.get(url).then(function(res){
              for(var i=0;i<res.data.data.length;i++){
                self.typeNoOptions[i]={
                  id:res.data.data[i].id,
                  value:res.data.data[i].typeNo,
                  label:res.data.data[i].typeName
                }
              }
            }).catch(function(err){
              console.log(err);
            })
        },
        //   getTypeOptions(){
        //   var self=this;
        //   carApi.carOption().then(res=>{
        //     console.log("封装请求")
        //     console.log(res.data)
        //   }).catch(err=>{
        //     console.log(err);
        //   })
        // },
        getTypeCount(val){
          console.log("val")
          console.log(val)
          var self=this;
          self.enterExitCount.length=Number(val);

        }
        },
        mounted:function(){
            this.getCarMsg();
            this.getTypeOptions();
        },
        created:function(){
        }
      }
    </script>
